<template>
  <div id="userManage">
    <QueryBox @searchSrldId="searchSrldId"></QueryBox>
    <div class="tab_box">
      <Table ref="table" border stripe :loading="loading" :columns="columns1" :data="data1"  size="small" highlight-row :max-height="tableHeight"></Table>
    </div>
    <div class="page_box" id="pageBox">
      <Page :total="total" :page-size="pageSize" show-elevator @on-change="pageChange" size="small" show-total/>
    </div>
    <Modal v-model="modal1"
           :mask-closable="true">
      <Form :model="formItem1" ref="auditModal" :rules="ruleValidate" :label-width="80" style="margin-top: 40px;">
        <FormItem prop="audit" label="审核">
          <RadioGroup v-model="formItem1.audit">
            <Radio label="2">审核通过</Radio>
            <Radio label="3">审核拒绝</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="邮箱地址" prop="email" v-if="formItem1.audit === '3'">
          <Input v-model="formItem1.email" placeholder="请输入邮箱地址"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { infoSelect, audit } from '@/api/dataCenter'
import QueryBox from './template_query'
import { renderTime } from '@/utils/common'
export default {
  name: 'dataCenterLc',
  data () {
    return {
      /* 查询条件 */
      name: '',
      /* 表格动态高度 */
      tableHeight: window.innerHeight - 290,
      /* 每页条数 */
      pageSize: Math.floor((window.innerHeight - 290) / 40) - 1,
      /* 页码 */
      pageNum: 1,
      /* 页面总条数 */
      total: 0,
      /* 表格 */
      columns1: [
        {
          title: '序号',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        // {
        //   title: '版本名称',
        //   key: 'version_id'
        // },
        {
          title: '服务名称',
          key: 'name',
          align: 'center'
        },
        {
          title: '审核状态',
          align: 'center',
          width: 120,
          key: 'audit',
          render: (h, params) => {
            let auditStatus = params.row.audit
            let btnType = ''
            if (auditStatus === 0) {
              auditStatus = '无需审核'
              btnType = 'default'
            } else if (auditStatus === 1) {
              auditStatus = '待审核'
              btnType = 'primary'
            } else if (auditStatus === 2) {
              auditStatus = '审核通过'
              btnType = 'success'
            } else if (auditStatus === 3) {
              auditStatus = '审核未通过'
              btnType = 'error'
            }
            return h('div', [
              // 审核
              h('Tag', {
                props: {
                  color: btnType
                }
              }, auditStatus)
            ])
          }
        },
        {
          title: '变更时间',
          key: 'updatetime',
          render: (h, params) => {
            return h('span', renderTime(params.row.updatetime))
          },
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          width: 100,
          align: 'center',
          render: (h, params) => {
            let status = false
            if (params.row.audit === 1) {
              status = false
            } else {
              status = true
            }
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'ios-funnel',
                  disabled: status
                },
                on: {
                  click: () => {
                    this.delId = params.row.id
                    this.openAdd()
                  }
                }
              }, '审核')
            ])
          }
        }
      ],
      data1: [],
      delId: '',
      auditStatus: '',
      /* 加载 */
      loading: false,
      formItem1: {
        audit: '',
        email: ''
      },
      ruleValidate: {
        email: [
          { required: true, message: '邮箱地址不能为空', trigger: 'blur' },
          { type: 'email', message: '邮箱地址格式不正确', trigger: 'blur' }
        ]
      },
      modal1: false
    }
  },
  components: {
    QueryBox
  },
  mounted () {
    /* 监听浏览器高度 */
    window.onresize = function () {
      this.tableHeight = window.innerHeight - 290
      this.pageSize = Math.floor((window.innerHeight - 290) / 40) - 1
    }.bind(this)
    this.getTableData()
  },
  methods: {
    /* axios: 获取列表数据 */
    getTableData () {
      this.loading = true
      infoSelect({
        data: {
          pageIndex: this.pageNum,
          pageSize: this.pageSize,
          name: this.name
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.poList
          this.total = res.data.data.poSum
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 查询框 */
    searchSrldId (obj) {
      this.name = obj.name
      this.pageNum = 1
      this.getTableData()
    },
    /* 分页跳转 */
    pageChange (p) {
      this.pageNum = p
      this.getTableData()
    },
    /* axios: 删除数据 */
    addSubmit () {
      this.$refs.auditModal.validate(valid => {
        if (valid) {
          audit({
            data: {
              id: this.delId,
              audit: this.formItem1.audit,
              email: this.formItem1.email
            }
          }).then((res) => {
            if (res.data.code === '0000') {
              this.getTableData()
              if (this.formItem1.audit === '2') {
                this.$Notice.success({ title: '审核通过成功' })
              }
              if (this.formItem1.audit === '3') {
                this.$Notice.success({ title: '审核拒绝成功' })
              }
              this.modal1 = false
            } else {
              if (this.formItem1.audit === '2') {
                this.$Notice.error({ title: '审核通过失败' })
              }
              if (this.formItem1.audit === '3') {
                this.$Notice.error({ title: '审核拒绝失败' })
              }
              this.modal1 = false
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
          })
        }
      })
    },
    openAdd () {
      this.modal1 = true
      this.formItem1.audit = ''
      this.formItem1.email = ''
      this.$refs.auditModal.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
  #userManage{
    .from_box{
      padding:10px;
      text-align: left;
    }
    .tab_box{
      padding: 0 15px;
    }
    .page_box{
      width: 100%;
      padding: 15px;
      text-align: right
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      margin-left: 10px;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
  }
</style>
